.recipe {
    border-radius: 10px;
    box-shadow: 0px 5px 20px rgb(71,71,71);
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background: white;
    align-items: center;
    min-width: 40%;
}

.image {
    border-radius: 10%;
    width: 300px;
    height: 250px;
}

.calories {
    font-weight: bold;
}

.ingredients {
    padding: 20px 30px 20px 30px;
    list-style: none;
    margin: auto;
}

a {
    text-decoration: none;
    color: black;
    margin: 0px 0px 20px 0px;
}

.sourceLabel {
    font-weight: bold;
}

.sourceLink{
    text-decoration: underline;
}